<template>
  <el-container>
    <!--  -->
    <el-header>
      <!-- logo -->
      <div class="indexleft">
        <el-menu router active-text-color="#0000FE " class="el-menu-demo" mode="horizontal" :default-active="activeIndex"
          @select="handleSelect" style="width: 100px;display: flex;justify-content: left;">

          <a href="http://www.xkxxkx.cn"><img src="../static/logo.jpg" style="width: 40px;height: 40px;margin: 12px 40px 0 50px;" /></a>
          <el-menu-item index="/index/index"> </el-menu-item>
          <el-menu-item index="/index/article"> </el-menu-item>
          <el-menu-item index="/index/MovieList"> </el-menu-item>
          <el-menu-item index="/index/about"> </el-menu-item>
          <el-menu-item index="/index/other"> </el-menu-item>
        </el-menu>
      </div>
      <div class="indexright">
        <!--  -->
        <ul class="ulright">
          <li>
            <el-menu v-if="loginFlag" class="el-menu-demo" mode="horizontal">
              <el-menu-item index="4" @click='logout()' class="el-icon-switch-button" target="_blank"> </el-menu-item>
            </el-menu>
          </li>
          <li>
            <el-menu v-if="loginFlag" class="el-menu-demo" mode="horizontal" :default-active="activeIndex" @select="handleSelect">
              <el-submenu index=null>
                <template slot="title"><i class="el-icon-tickets"></i></template>
                <el-menu-item index="/index/writearticle"><i class="el-icon-edit"></i> </el-menu-item>
                <el-menu-item index="/index/myarticle"@click="toMyArticle"><i class="el-icon-tickets"></i></el-menu-item>
              </el-submenu>
            </el-menu>
          </li>
          <li>
            <el-menu v-if="loginFlag" class="el-menu-demo" mode="horizontal" :default-active="activeIndex" @select="handleSelect"
              router>
              <el-submenu index=null>
                <template slot="title" :name.sync='name'><i class="el-icon-s-custom"></i>{{name}}</template>
                <el-menu-item index="/index/info/"></el-menu-item>
                <el-menu-item index=""></el-menu-item>
                <el-menu-item index="">...</el-menu-item>
              </el-submenu>
            </el-menu>
          </li>
          <li v-if="loginFlag" style="width: 70px;padding: 5px 0 0 0;">
            <el-row class="demo-avatar demo-basic">
              <el-col :span="12">
                <div class="demo-basic--circle">
                  <div class="block">
                    <el-avatar :size="50" :src="headImg"></el-avatar>
                  </div>
                </div>
              </el-col>
            </el-row>
          </li>
          <li>
            <!--  -->
            <el-row v-if="!loginFlag">
              <el-button type="primary" @click='login()'></el-button>
            </el-row>
          </li>
        </ul>
      </div>
    </el-header>
    <!--  -->
    <el-main>
      <!-- update this.$emit('updatename') -->
      <router-view @updatename="updatname($event)"></router-view>
    </el-main>
    <el-footer height="100px" style="font-size: 12px;text-align: center;">
      <span><a href="https://github.com/1310744545"><img src="https://github.com/fluidicon.png" style="height: 40px;width: 40px;"></a></span><br>
      <span>Copyright @2020 1310744545</span><br>
      <span><a href="http://beian.miit.gov.cn/" style="text-decoration: none;">ICP20000673-1</a></span>
    </el-footer>
  </el-container>
</template>

<script>
  export default {
    data() {
      return {
        loginFlag: false,
        activeIndex: '/index/index',
        id:window.localStorage.getItem('id'),
        name: '',
        headImg: '',
        defaultHeight:{
          height:''
        }
      }
    },
    methods: {
      login() { //
        this.$router.push('/login');
      },
      logout() { //
        this.$http.post('/logout');
        this.loginFlag = false;
        window.localStorage.clear();
        window.sessionStorage.clear();
        this.$router.push('/index/index');
      },
      handleSelect(key, keyPath) {
        // console.log(key);
        // console.log(keyPath);
        if (key != this.activeIndex) { //
          this.$router.push(key); //
        }
        this.activeIndex = key;
        window.sessionStorage.setItem("myrouter", key);
      },
      loginchick() { //
        this.loginFlag = window.localStorage.getItem('loginFlag');
      },
      searchUser() {
        if (this.loginFlag) {
          this.$http.post('/God', {
            'id': this.id
          }).then(dat => {
            this.name = dat.data.name;
            this.headImg = dat.data.headImg;
          })
        }
      },
      updatname($event) {
        this.name = $event
      },
      updaterouter() {
        this.activeIndex = window.sessionStorage.getItem("myrouter");
      },
      toMyArticle(){
        this.$router.push({
                  path: `/index/myarticle/${this.id}`,
                })
      }
  },
  created() {
    this.loginchick();
    this.searchUser();
  },
  mounted() {
    this.updaterouter();
  }
  }
</script>

<style scoped>
  .el-header {
    padding: 0;
    margin: 0;
    background-color: rgb(255, 255, 255);
    font-size: 30px;
    display: flex;

    justify-content: space-between;
    border-color: #C8CBCF;
    ;
  }

  .indexleft,
  .indexright {
    width: 50%;
  }

  .ulright {
    margin: 0;
    padding: 0;
  }

  .ulright>li {
    list-style: none;
    float: right;
    width: 135px;
  }

  .el-main {
    background-color: rgb(245, 247, 249);
    /* height: 100%; */
    width: 100%;
    /* background: url(../static/background.jpg) no-repeat 0px 0px; */
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
    /* min-height: 100vh; */
    font-family: 'Roboto', sans-serif;
    overflow: hidden;

  }

  el-button {
    left: 0;
  }
a{
  -webkit-tap-highlight-color: rgba(255, 255, 255, 0);
  -webkit-user-select: none;
  -moz-user-focus: none;
  -moz-user-select: none;
}
img{
  width: 100%;
  height: 100%;
}

</style>
