<template>
  <div class="login_container">
    <div class="login_box">
      <div class="title"></div>
      <!--  ref-->
      <el-form ref="loginFormRef" class="login_form" :model="loginForm" :rules="loginFormRules" @keyup.enter.native="login">
        <!---->
        <el-form-item prop="count">
          <el-input v-model="loginForm.count" prefix-icon="el-icon-user-solid" placeholder=""></el-input>
        </el-form-item>
        <!---->
        <el-form-item prop="password">
          <el-input v-model="loginForm.password" prefix-icon="el-icon-lock" placeholder="" type="password"></el-input>
        </el-form-item>
        <!---->
        <el-form-item class="btns">
          <el-button @click='login' type="primary"></el-button>
          <el-button @click='register'></el-button>
        </el-form-item>
        <!-- <span id="qqLoginBtn" @click="qqLogin">asd</span> -->
      </el-form>
    </div>
  </div>
</template>

<script>
  // import '../static/login.css'
  // import '../static/qc_jssdk.js'

  export default {
    data() {
      return {
        loginForm: {
          count: '',
          password: ''
        },
        loginFormRules: {
          count: [{
              required: true,
              message: '',
              trigger: 'blur'
            },
            {
              min: 6,
              max: 16,
              message: ' 6  16 ',
              trigger: 'blur'
            }
          ],
          password: [{
              required: true,
              message: '',
              trigger: 'blur'
            },
            {
              min: 6,
              max: 16,
              message: ' 6  16 ',
              trigger: 'blur'
            }
          ]
        }
      }
    },
    methods: {
      register() {
        this.$router.push('/register');
      },
      login() {
        this.$refs.loginFormRef.validate(async valid => { //this.$refs.loginFormRef,validate
          if (!valid) { //,  
            return;
          } else { //,
            const result = await this.$http.post('login', this.loginForm)
              .then((dat) => { //dat
                if (dat.data.code == 400) { //
                  this.$message({
                    message: '',
                    type: 'error',
                    duration: 1500,
                    showClose: true
                  })
                } else if (dat.data.code == 200) { //,
                  this.$message({
                    message: '',
                    type: 'success',
                    duration: 1500,
                    showClose: true
                  });
                  window.localStorage.setItem('loginFlag', true)
                  window.localStorage.setItem('id', dat.data.id)
                  window.localStorage.setItem('myrouter', '/index/index')
                  this.$router.push({
                    path: '/index/index'
                  })

                } else {
                  this.$message({
                    message: '',
                    type: 'error',
                    duration: 1500,
                    showClose: true
                  });
                }
              })
          }
        })
      }
      // ,
      // qqLogin() {
      //   // 
      //   QC.Login.showPopup({
      //     appId: '101874222',
      //     redirectURI: 'xkxxkx.cn'
      //   })
      // }
    }
  }
</script>

<style scoped>
  .login_container {
    height: 900px;
    width: 100%;
    background: url(../static/background.jpg) no-repeat 0px 0px;
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
    min-height: 100vh;
    font-family: 'Roboto', sans-serif;
    overflow: hidden;
  }

  .login_box {
    width: 400px;
    height: 400px;
    background-color: white;
    border-radius: 20px;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  }

  .title {
    height: 50px;
    font-size: 50px;
    margin: 30px 0 80px 37%;
  }

  .login_form {
    position: absolute;
    bottom: 15%;
    width: 100%;
    padding: 0 20px;
    box-sizing: border-box;
  }

  .btns {
    display: flex;
    justify-content: center;
  }

  >>>.el-input {
    border-color: #0000FE;
  }
</style>
